import React from "react";

import "./aboutWe.scss";

function aboutWe() {
  return (
    <div data-aos="fade-up" data-aos-delay="300">
    <div className="aboutWe" id="aboutWe">
      <div className="aboutWe-content">
        <h1>О нашей работе</h1>
        <p>
          11 апреля - Международный день освобождения узников фашистских
          концлагерей, отмечаемый ежегодно по инициативе ООН. Среди огромного
          числа жертв Второй мировой войны есть особая категория лиц - бывшие
          малолетние и несовершеннолетние узники нацизма, угнанные на чужбину.
          Они были детьми, но их угоняли (часто вместе с матерями), принуждали к
          тяжёлой физической работе, у них забирали кровь для солдат Вермахта,
          ставили на них медицинские эксперименты. В самой беззаботной и
          радостной жизненной поре они под дулом автоматов вместе со взрослыми
          испытали на себе непосильный труд, голод, холод, болезни и смерть.
          Преступление против детства - одно из страшных деяний нацистов,
          которое было осуждено на Нюрнбергском процессе. Старшего поколения
          узников уже нет: время, к сожалению, неумолимо. А бывшие дети -
          сегодня пожилые, нередко больные люди, с трудной послевоенной судьбой,
          нуждающиеся в помощи и заботе. </p>
          <p>Мы - Реер Каролина, Силин Юрий -
          участники поисковой группы объединения по интересам «Гражданин нового
          века» УО «Новополоцкий государственный политехнический колледж» под
          руководством педагогов Антоненковой Ирины Павловны,
          Леоновой Ирины Геннадьевны в течение полутора лет собирали материал о
          малолетних узниках нацизма. Они – новополочане, поразному связанные с
          историей нашего колледжа. Главное, они еще живы и могут рассказать нам
          о своей жизни. Собранный материал мы оформляем в виде
          исследовательской проектной работы, которая является информационно -
          достоверным актуальным материалом, потому что это реальные истории.
          <p>Актуальность темы обусловлена тем, что свидетелей
          человеконенавистнической политики нацизма остается все меньше, и нужно
          спешить помочь им оставить потомкам урок мужества и стойкости.</p>
        </p>
      </div>
    </div>
    </div>
  );
}

export default aboutWe;
