import { Spinner } from "@chakra-ui/react";
import React, { Suspense } from "react";

import Navbar from "./components/navbar";
import Main from "./pages/main/main";

import ContactMe from "./pages/ContactMe";

import Prisoners from "./pages/Prisoners";
import MeaninghSite from "./pages/MeaninghSite";
import Сonclusions from "./pages/Сonclusions";
import KidFade from "./pages/KidFade";
import WeNovopolock from "./pages/weNovopolock";
import AboutWe from "./pages/aboutWe";
import Genocid from "./pages/Genocide";

import AOS from "aos";
import "aos/dist/aos.css";

import SVG from "react-inlinesvg";
import waveSVG1 from "./assets/img/waves1.svg";
import waveSVG2 from "./assets/img/waves2.svg";
import waveSVG3 from "./assets/img/waves3.svg";
import waveSVG4 from "./assets/img/waves4.svg";

import "normalize.css";
import "./app.scss";

function App() {
  AOS.init();
  return (
    <Suspense fallback={<Spinner />}>
      <div className="App">
        <Navbar />
        <Main />
        <AboutWe />
        <SVG
          src={waveSVG1}
          style={{
            transform: "rotate(180deg)",
          }}
          className="page1-divider"
        />
        <Genocid />
        <SVG className="page1-divider" 
        
        src={waveSVG2} />
        <Prisoners />
        <SVG
          className="page1-divider"
          src={waveSVG4}
          style={{
            transform: "rotate(180deg)",
            position:'relative', bottom:'-1px'
          }}
        />
        <WeNovopolock />
        <SVG className="page1-divider" src={waveSVG2} />
        <KidFade />
        <SVG
          className="page1-divider"
          src={waveSVG4}
          style={{
            transform: "rotate(180deg)",
          }}
        />
        <MeaninghSite />
        <SVG className="page1-divider" src={waveSVG2} />
        <Сonclusions />
        <SVG
          className="page1-divider"
          style={{
            transform: "rotate(180deg)",
            position: "relative",
            bottom: "-1px",
          }}
          src={waveSVG2}
        />
        <ContactMe />
      </div>
    </Suspense>
  );
}

export default App;
