import React from 'react'
import './Сonclusions.scss'

function Сonclusions() {
    return (
        <div className='conclusions'><h1>Выводы авторов</h1>
            <p>По-разному сложились послевоенные судьбы
                наших собеседников, но у всех у них есть самое
                заветное желание: чтобы они были последним
                поколением, у которого война отняла детство и
                здоровье.
                Собирая материал, встречаясь с бывшими малолетними узниками, которые пережили застенки концлагерей, нацистский плен, мы пришли к
                следующим выводам:
            </p>
            <ul>
                <li>Не только взрослые, но и дети страдали в годы
                    войны. Выжить им удалось, в основном, благодаря присутствию рядом матерей, которые отдавали
                    детям последние крохи хлеба, всегда готовы были
                    прийти на помощь и закрыть своим телом. Выжили в основном те дети, кто был с родителями.
                </li>
                <li>Не обошлось и без помощи добрых людей. Среди
                    немцев были изверги и были те, кто оказывал помощь людям в это тяжелое время. Люди любой
                    национальности делятся на добрых и злых.
                </li>
                <li>Немаловажную роль сыграла и доля случайности.</li>
                <li>Степень влияния трагического детства на дальнейшую судьбу бывших малолетних узников, конечно
                    же, велика, ведь до сих пор эти люди не могут
                    вспоминать тот период своей жизни без слез. У
                    кого – то подорвано здоровье; кто – то не смог реализовать свои планы. Но, тем не менее, их дух
                    не сломлен, в большей степени благодаря их личным качествам. Большинство из них продолжают
                    жить и трудиться на благо общества.
                </li>
                <li>К ещё одному печальному выводу мы пришли. В
                    юбилейные годы выходило много монографий по
                    истории Второй мировой и Великой Отечественной войн, особенно в середине 80 – х годов. Писалось, исследовалось и говорилось о битвах, о партизанском движении, движении Сопротивления,
                    помощи тыла фронту и очень мало уделялось внимания тому факту, что сотни тысяч людей были
                    вывезены насильно за пределы СССР и вынужден были жить в рабских условиях.</li>
                <li>О детях войны стали говорить только в последние
                    десятилетия. Связано это с политикой демократизации общества нашего суверенного государства;
                    с пониманием того, что дети не виноваты в том,
                    что с ними произошло, ведь они являлись только
                    жертвами войны, а не врагами и предателями
                    своей Родины.</li>
                <li>Получив новые знания о войне через рассказы
                    очевидцев, мы сделали вывод, что любая война
                    несет бедствия и страдания людям, любая война
                    – это страшно, а нацизм ещё страшнее.
                </li>
                <li> Человечество должно приложить усилия, чтобы
                    войны не повторились.</li>
            </ul>
        </div >
    )
}

export default Сonclusions