import React from "react";
import "./MeaninghSite.scss";

function MeaninghSite() {
  return (
    <div className="meaninghSite">
    <div className="meaninghSite-content">
      <h1>Практическая значимость сайта</h1>
      <p>Практическое значение работы заключается в том, что:</p>
      <ul style={{ listStyle: "inside" }}>
        <li>
          эта тема была освещена на занятиях объединения по интересам «Гражданин
          нового века» и в ходе недели гражданскопатриотического воспитания;
        </li>
        <li>на воспитательных часах в учебных группах колледжа;</li>
        <li>на заседании Краеведческой гостиной;</li>
        <li>
          стала составной частью постоянно действующего в колледже
          исследовательского проекта «История без белых пятен»;
        </li>
        <li>участвовала в онлайн-проекте «Портрет Победы»;</li>
        <li>
          имена наших героев рекомендованы для занесения в Летопись
          международного проекта «Жизнь продолжается… Место встречи: диалог»;
        </li>
        <li>наш поиск и наши находки продолжаются;</li>
        <li>
          материалы интервью- беседы с И.Е.Овсянкиной выложены на сайте
          Витебского УМЦ, в проекте «Помнить прошлое, чтобы верить в будущее»,
          этап «Дети войны».
        </li>
      </ul>
      <p style={{ marginTop: "20px" }}>
        Следуя цели своего исследования, мы старались раскрыть античеловеческий
        характер нацистской идеологии. С каждым годом всё меньше и меньше
        становится участников и свидетелей Второй мировой и Великой
        Отечественной войн. Люди начинают забывать тех, кто спас нашу землю от
        оккупантов. Мало мы знаем о тех, кто побывал в гитлеровских концлагерях.
        Многим жертвам плена, оставшимся в живых, сегодня Германия выплачивает
        компенсацию, но уже никто не сможет вернуть этим людям их детство. Быть
        практически одиноким и, несмотря на это, суметь выжить в тяжёлых
        условиях оккупационного режима, бороться с трудностями, наверное, смог
        бы не каждый ребёнок. Наши собеседники смогли.
      </p>
      <p>
        Данное исследование осуществлялось в течение полутора лет, оно не
        претендует на исчерпывающее освещение темы. Интерес к данной теме
        заставляет нас продолжать работу в следующем направлении: оказать
        посильную помощь в публикации воспоминаний наших земляков в городской
        газете «Новополоцк сегодня» и пролонгировать исследовательский проект в
        целом.
      </p>
    </div>
    </div>
  );
}

export default MeaninghSite;
