import React from "react";
import "./Prisoners.scss";

import HTMLFlipBook from "react-pageflip";
import bookIMG from "../../assets/img/book.jpg";
import prisonersBook from "../../assets/img/prisonersBook.jpg";
import prisonersBook2 from "../../assets/img/prisonersBook2.jpg";

import "swiper/scss";
import "swiper/scss/pagination";

function Prisoners() {
  return (
    <div
      data-aos="fade-left"
      data-aos-offset="300"
      data-aos-easing="ease-in-sine"
      data-aos-delay="300"
    >
      <HTMLFlipBook
        width={600}
        height={800}
        style={{
          margin: "0 auto",
          boxShadow: "0 25px 50px -12px rgba(0, 0, 0, 0.25)",
        }}
      >
        <img src={prisonersBook} className="prisoners-book-cover" alt="" />

        <div className="prisoners-book">
          <h2>Малолетние узники нацизма, кто они?</h2>
          <p>
            В годы Второй мировой войны в нацистских лагерях, гетто, других
            местах принудительного содержания погибло более 11 миллионов
            советских людей – мужчин, женщин и детей, а почти 5 миллионов - были
            увезены из – под родного крова и превращены в рабов гитлеровского
            рейха. Кто же такие малолетние узники нацизма? Существуют ли
            нормативно – правовые документы, регламентирующие их положение?
          </p>
        </div>

          <div className="prisoners-book">
          <p>
            От Овсянкиной И.Е. мы узнали, что такие документы существуют только
            в России. На сайте Министерства труда и социального развития
            Российской Федерации мы нашли Постановление от 07.07.99 г. № 20,
            согласно которому малолетними узниками считаются «...проживающие на
            территории РФ бывшие несовершеннолетние граждане, которые в годы
            второй мировой войны в возрасте до 18 лет содержались или родились в
            концлагерях, гетто, других местах принудительного содержания,
            созданных фашистами и их союзниками на территориях Германии и
            союзных с нею стран, а также на оккупированных ими территориях
            бывшего СССР и стран Европы».
          </p>
          </div>
        <div className="prisoners-book">
        <p>
            Существует еще один сайт, на котором бывшие узники, в том числе и
            малолетние, высказывают своё мнение о необходимости создания
            правовой базы их положения и в Беларуси. Дети, угнанные в нацистскую
            неволю, содержались наравне со взрослыми, причем в условиях
            тюремного режима, в концентрационных и трудовых лагерях, находились
            под стражей.Эти документы также говорят о своевременности и
            актуальности нашего исследования.
          </p>
          </div>

        <div className="prisoners-book">

          <p>
            Анализируя собранный в ходе исследования фактический материал, мы
            столкнулись с разными типами нацистских лагерей: концентрационный
            лагерь, лагерь – тюрьма, трудовой лагерь, транзитный лагерь, лагерь
            смерти. По воспоминаниям наших собеседников мы сделали вывод, что
            они находились в трудовых лагерях. В трудовых лагерях содержалось, в
            основном, угнанное с оккупированных территорий трудоспособное
            население, женщины, а также дети с 11 лет. В таких лагерях реже
            использовались жестокие телесные наказания и размер
            продовольственного пайка был рассчитан на минимальное поддержание
            рабочей силы.
          </p>
          </div>
        <img src={prisonersBook2} alt="" />
      </HTMLFlipBook>
    </div>
  );
}

export default Prisoners;
